.header {
	@include image(bg-header);
	@include cover;
	padding: 15px 0;
	&__logo {
		float: left;
		.icon-logo-meduzabrush {
			fill: $white; } }
	//nav
	&__nav {
		display: inline-block;
		vertical-align: top;
		margin-top: 20px;
		float: right;
		position: relative;
		@include clr;
		@include r(992) {
			margin-left: 0; } } }

// Logo
.logo {
	text-decoration: none;
	width: 130px;
	height: 50px;
	display: inline-block;
	vertical-align: top;
	svg {
		width: 100%;
		height: 100%; } }

// list
.list {
	&-lang {
		float: right;
		margin-left: 75px; } }
