html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

.icon-logo-meduzabrush {
  width: 2.5em;
  height: 1em;
  fill: initial;
}

body {
  background: #FFF;
  line-height: 1.4;
}

.inner {
  max-width: 1170px;
  padding: 0 25px;
  margin: 0 auto;
}

.inner:after {
  content: " ";
  display: table;
  clear: both;
}

.order {
  padding: 100px 0 95px 0;
  background-image: url("../img/bg-order.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  text-align: center;
}

.order h3 {
  margin-bottom: 70px;
  text-transform: uppercase;
  color: #162332;
  font-size: 36px;
}

.order form {
  max-width: 770px;
  margin: 0 auto;
  font-size: 0;
}

.order form input {
  display: inline-block;
  vertical-align: top;
  margin: 0;
  font-size: 16px;
  width: 100%;
  max-width: calc(50% - 15px);
  padding: 0;
  background-color: #c8b29a;
  box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, 0.11);
  border: 1px solid #fff;
  letter-spacing: 0.200rem;
  text-transform: uppercase;
  padding: 24px 0 24px 38px;
}

.order form input::placeholder {
  color: #fff;
}

@media only screen and (max-width: 640px) {
  .order form input {
    max-width: calc(50% - 10px);
  }
}

.order form input.name {
  margin-right: 30px;
}

@media only screen and (max-width: 640px) {
  .order form input.name {
    margin-right: 20px;
  }
}

.order form textarea {
  width: 100%;
  resize: none;
  font-size: 16px;
  margin: 30px auto 55px;
  border: 1px solid #fff;
  box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, 0.11);
  background-color: #c8b29a;
  padding: 24px 0 24px 38px;
  letter-spacing: 0.200rem;
  text-transform: uppercase;
}

.order form textarea::placeholder {
  color: #fff;
}

.header {
  background-image: url("../img/bg-header.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding: 15px 0;
}

.header__logo {
  float: left;
}

.header__logo .icon-logo-meduzabrush {
  fill: #fff;
}

.header__nav {
  display: inline-block;
  vertical-align: top;
  margin-top: 20px;
  float: right;
  position: relative;
}

.header__nav:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 992px) {
  .header__nav {
    margin-left: 0;
  }
}

.logo {
  text-decoration: none;
  width: 130px;
  height: 50px;
  display: inline-block;
  vertical-align: top;
}

.logo svg {
  width: 100%;
  height: 100%;
}

.list-lang {
  float: right;
  margin-left: 75px;
}

.menu {
  font-size: 0;
}

.menu__item {
  display: inline-block;
  vertical-align: middle;
  margin: 0 25px;
}

.menu__link {
  text-decoration: none;
  text-transform: uppercase;
  color: #fff;
  font-size: 12.02px;
  position: relative;
  display: inline-block;
  vertical-align: middle;
  padding-bottom: 19px;
}

.menu__link:after {
  content: "";
  display: block;
  position: absolute;
  top: 100%;
  left: 50%;
  opacity: 0;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  width: 0;
  height: 1px;
  transition: all .24s ease-in-out;
  background-color: rgba(200, 178, 154, 0.3);
  -webkit-transition: all .24s ease-in-out;
}

.menu__link.is-current:after {
  display: block;
  background-color: #c8b29a;
  opacity: 1;
  width: 100%;
}

.menu__link:hover:after {
  opacity: 1;
  width: 100%;
}

.menu.header_menu {
  float: left;
}

.footer {
  background-color: #fff;
  padding: 55px 0;
}

@media only screen and (max-width: 640px) {
  .footer {
    text-align: center;
    padding: 59px 0 48px 0;
  }
}

@media only screen and (max-width: 481px) {
  .footer {
    padding: 30px 0;
  }
}

.footer .inner {
  padding: 0;
}

@media only screen and (max-width: 1266px) {
  .footer .inner {
    padding: 0 25px;
  }
}

@media only screen and (max-width: 640px) {
  .footer .inner {
    padding: 0 55px;
  }
}

@media only screen and (max-width: 481px) {
  .footer .inner {
    padding: 0 30px;
  }
}

.footer__logo {
  float: right;
}

.footer__logo .icon-logo-meduzabrush {
  fill: #162332;
}

@media only screen and (max-width: 640px) {
  .footer__logo {
    margin-top: 45px;
  }
}

@media only screen and (max-width: 481px) {
  .footer__logo {
    float: none;
  }
}

.footer__info {
  display: inline-block;
  vertical-align: top;
}

.footer__info:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 640px) {
  .footer__info {
    text-align: left;
    float: left;
  }
}

@media only screen and (max-width: 481px) {
  .footer__info {
    text-align: center;
    width: 100%;
  }
}

.footer .contacts {
  display: inline-block;
  text-align: left;
  float: left;
  margin-right: 69px;
  vertical-align: top;
}

@media only screen and (max-width: 640px) {
  .footer .contacts {
    margin-right: 0;
    margin-bottom: 45px;
    float: none;
    text-align: inherit;
  }
}

.footer .contacts p {
  color: #7e8186;
  margin-bottom: 17px;
  font-size: 16px;
}

.footer .contacts p:last-child {
  margin-bottom: 0;
}

.footer .contacts p a.numb {
  color: inherit;
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
}

.footer .address {
  display: inline-block;
  vertical-align: top;
}

@media only screen and (max-width: 640px) {
  .footer .address {
    display: block;
  }
}

.footer .address p, .footer .address a {
  color: #7e8186;
  font-size: 16px;
}

.footer .address a {
  text-decoration: none;
  display: inline-block;
  vertical-align: top;
}

.footer .address a span {
  font-weight: bold;
}

.footer .address p {
  margin-bottom: 17px;
}

.footer .address p:last-child {
  margin-bottom: 0;
}

.btn-ghost {
  border-color: inherit;
  background-color: transparent;
  position: relative;
}

.btn-ghost:after, .btn-ghost:before {
  content: "";
  display: block;
  position: absolute;
  background-color: transparent;
}

.btn-ghost:after {
  right: 0;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: #958779;
}

.btn-ghost:before {
  background-color: #958779;
  width: 100%;
  left: 0;
  top: 100%;
}

.btn-nav {
  display: none;
  position: absolute;
  top: 0;
  font-size: 0;
  outline: none;
  left: 0;
  background-color: transparent;
  background-image: none;
  width: 30px;
  height: 30px;
}

.btn-nav:hover span {
  background-color: transparent;
}

.btn-nav span {
  position: absolute;
  -webkit-transition: all .24s ease-in-out;
  transition: all .24s ease-in-out;
  background-color: #958779;
  width: 100%;
  height: 4px;
  top: 0;
  left: 0;
  border-radius: 4px;
}

.btn-nav span:after, .btn-nav span:before {
  content: '';
  display: block;
  border-radius: 4px;
  position: absolute;
  background-color: #958779;
  -webkit-transition: all .24s ease-in-out;
  transition: all .24s ease-in-out;
  width: 100%;
  height: 100%;
}

.btn-nav span:after {
  top: 13px;
}

.btn-nav span:before {
  top: 26px;
}

@media only screen and (max-width: 768px) {
  .btn-nav {
    display: block;
  }
}
