.order {
	padding: 100px 0 95px 0;
	@include image(bg-order);
	@include cover;
	text-align: center;
	h3 {
		margin-bottom: 70px;
		text-transform: uppercase;
		color: $main;
		font-size: $text-lg; } //36px
	form {
		max-width: 770px;
		margin: 0 auto;
		font-size: 0;
		input {
			display: inline-block;
			vertical-align: top;
			margin: 0;
			font-size: $text-reg; //16px
			width: 100%;
			max-width: calc(50% - 15px);
			padding: 0;
			@include placeholder-color($white);
			background-color: $coffee;
			box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, .11);
			border: 1px solid $white;
			letter-spacing: 0.200rem;
			text-transform: uppercase;
			padding: 24px 0 24px 38px;
			@include r(640) {
				max-width: calc(50% - 10px); }
			&.name {
				margin-right: 30px;
				@include r(640) {
					margin-right: 20px; } } }
		textarea {
			width: 100%;
			resize: none;
			font-size: $text-reg; //16px
			margin: 30px auto 55px;
			border: 1px solid $white;
			@include placeholder-color($white);
			box-shadow: inset 0 0 30px 5px rgba(62, 53, 42, .11);
			background-color: $coffee;
			padding: 24px 0 24px 38px;
			letter-spacing: 0.200rem;
			text-transform: uppercase; } } }
