.btn {
	&-ghost {
		border-color: inherit;
		background-color: transparent;
		position: relative;
		&:after, &:before {
			content: "";
			display: block;
			position: absolute;
			background-color: transparent; }
		&:after {
			right: 0;
			top: 0;
			width: 1px;
			height: 100%;
			background-color: $brown; }
		&:before {
			background-color: $brown;
			width: 100%;
			left: 0;
			top: 100%; } }
	&-brown {}
	&-nav {
		display: none;
		position: absolute;
		top: 0;
		font-size: 0;
		outline: none;
		left: 0;
		background-color: transparent;
		background-image: none;
		width: 30px;
		height: 30px;
		&:hover {
			span {
				background-color: transparent; } }
		span {
			position: absolute;
			-webkit-transition: all .24s ease-in-out;
			transition: all .24s ease-in-out;
			background-color: $brown;
			width: 100%;
			height: 4px;
			top: 0;
			left: 0;
			border-radius: 4px;
			&:after,
			&:before {
				content: '';
				display: block;
				border-radius: 4px;
				position: absolute;
				background-color: $brown;
				-webkit-transition: all .24s ease-in-out;
				transition: all .24s ease-in-out;
				width: 100%;
				height: 100%; }
			&:after {
				top: 13px; }
			&:before {
				top: 26px; } }
		@include r(768) {
			display: block; } } }
